import * as React from "react";

interface Props {
    text: string;
    isMainPage: boolean;
}
const Message = (props: Props) => {
    const { text, isMainPage } = props;

    return <p className={isMainPage ? "noHints search-text--dark" : "noHints--short search-text--dark"}>{text}</p>;
};

export default Message;
